import React from "react";
import _ from 'lodash'
import classNames from 'classnames'
import PortalWithState from 'react-portal'
import { Link } from 'react-router-dom'

import Modal from '../modals/Modal'
import Importer from '../../containers/Importer'
import Page from '../elements/Page'

import { getFileTypes, getTypesByGroup } from '../../scripts/fileTypes'
import button from '../../styles/buttons.scss'
import style from '../../styles/FuneralList.scss'
import styleRetraite from '../../styles/Retraite.scss'
import { DOCTYPE_DIAGNOSIS, DOCTYPE_EQUIPMENTS, DOCTYPE_HOMEDATA, DOCTYPE_MAINTENANCES, DOCTYPE_WORKS } from '../../constants'
import { getDataElements } from '../../tools'
import { DOCTYPE_CONTACTS, DOCTYPE_F_CONTACTS, DOCTYPE_F_CUSTOMCONTACTS, DOCTYPE_F_LASTWILL, DOCTYPE_META } from "../../constants/index";
import { getFields, getInfosFromDoctype } from "../../scripts/dataFields";
import DataForm from "../../containers/DataForm";
import { contactExist, getMeta, getCustomContactMeta } from "../../scripts/meta";
import { string_to_slug } from "../../tools/index";
import { getContactMeta } from "../../scripts/onboarding";
import customtypes from '../../constants/types.json'
import HoverImage from "react-hover-image"
import html2canvas from 'html2canvas';
import * as jsPDF  from 'jspdf'
import qs from "query-string";

export default class Retraite extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            caisse: '............................',
            numeroAdresse: '............................',
            rue: '',
            postale: '............................',
            ville: '',
            pays: '',
            telephone: '',
            telecopie: '',
            email: '',
            immatriculation: '............',
            faite: '..........................',
            recu: '',
            faitePar: '',
            error: ''
        };
        this.handleLetter = this.handleLetter.bind(this)
    }
  componentWillMount() {
    let id = qs.parse(this.props.location.search).id;

    cozy.client.data.find('io.cozy.contacts', id).then(res => {
      console.log(res);
      this.setState({data: res});
    });
  }

    printElem() {
        const input = document.getElementById('letter');
        html2canvas(input)
          .then((canvas) => {
            const dataURL  = canvas.toDataURL('image/jpeg');
            const downloadLink = document.createElement('a');
            downloadLink.href = dataURL;
            downloadLink.download = 'lettre-retraite.png';
            downloadLink.style.display = 'nones';
            document.body.appendChild(downloadLink);
            downloadLink.click();
            // console.log(imgData);
            // window.open(imgData);
            // const pdf = new pdfConverter('p', 'mm', 'a4');
            // pdf.fromHTML(window.document.getElementById('letter'), 10, 10,{'width': 180});
            // // pdf.addImage(imgData, 'JPEG', 0, 0);
            // // pdf.output('dataurlnewwindow');
            // pdf.save("download.pdf");
          })
        ;
    }

    handleLetter() {
        if (document.getElementById('caisse').value == '' || document.getElementById('numeroAddresse').value == '' || document.getElementById('rue').value == ''
            || document.getElementById('postale').value == '' || document.getElementById('ville').value == '' || document.getElementById('pays').value == ''
            || document.getElementById('telephone').value == '' || document.getElementById('telecopie').value == '' || document.getElementById('email').value == ''
            || document.getElementById('immatriculation').value == '' || document.getElementById('faite').value == '' || document.getElementById('recu').value == ''
            || document.getElementById('faitePar').value == '') {
            this.setState({
                error: "Veuillez complter l'intgralit des informations"
            })
        } else {
            this.setState({
                caisse: document.getElementById('caisse').value,
                numeroAdresse: document.getElementById('numeroAddresse').value,
                rue: document.getElementById('rue').value,
                postale: document.getElementById('postale').value,
                ville: document.getElementById('ville').value,
                pays: document.getElementById('pays').value,
                telephone: document.getElementById('telephone').value,
                telecopie: document.getElementById('telecopie').value,
                email: document.getElementById('email').value,
                immatriculation: document.getElementById('immatriculation').value,
                faite: document.getElementById('faite').value,
                recu: document.getElementById('recu').value,
                faitePar: document.getElementById('faitePar').value,
                error: ''
            })
        }
    }

    render() {
        return (
          this.state.data ?
            <Page title="Sahra Vadrot" subtitle="Cet espace vous permet de renseigner l'ensemble des informations qui seront utiles pour prvenir les caisses de retraite ">
                <div className={style.main}>
                    <div >
                        <div>
                            <h3 className={styleRetraite.titleRetraite} >Caisses de retraite</h3>
                        </div>
                        <p className={styleRetraite.dataTxt} >
                            Les caisses de retraite, principales et complmentaires, doivent tre informes du dcs pour interrompre les versements.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            Pour vrifier les droits au capital dcs si le dfunt est dcd dans les trois mois suivant la notification de retraite.
                            Ou faire valoir les droits  la pension de rversion pour le conjoint.
                        </p>
                        <p className={styleRetraite.dataTxt} >
                            Le conjoint survivant mme divorc, sil nest pas remari, peut bnficier, sous certaines conditions,
                            de la rversion des droits  la retraite du dfunt si celui-ci percevait une retraite ou pouvait y prtendre
                        </p>
                    </div>
                    <div>
                        <h3 className={styleRetraite.titleRetraite} >Coordonnes</h3>

                        <div className={styleRetraite.contentForm} >
                            <div>
                                <label>nom de la caisse de retraite</label>
                                <input type="text" id="caisse" placeholder="Caisse de retraite" className={styleRetraite.inputForm} />
                            </div>

                            <div>
                                <label>adresse</label>
                                <div className={styleRetraite.adresse}>
                                    <input type="text" id="numeroAddresse" placeholder="191" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                  <input type="text" id="rue" value={this.state.data.worker_address.way} className={[styleRetraite.inputForm, styleRetraite.sizeRue].join(' ')} />
                                </div>
                            </div>
                          <div>
                            <div className={styleRetraite.adresse}>
                              <input type="text" id="postale" value={this.state.data.worker_address.code} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                              <input type="text" id="ville" value={this.state.data.worker_address.city} className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="pays" placeholder="France" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                </div>
                            </div>
                            <div>
                                <div className={styleRetraite.labelAdresse}>
                                    <label>numro de tlphone</label>
                                    <label>numro de tlcopie</label>
                                    <label>email</label>
                                </div>
                                <div className={styleRetraite.adresse}>
                                    <input type="text" id="telephone" placeholder="01 84 16 32 56" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="telecopie" placeholder="01 84 16 32 57" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                    <input type="text" id="email" placeholder="contact@retraite.fr" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                </div>
                            </div>
                            <h3 className={styleRetraite.titleRetraite} >Information sur le contrat</h3>
                            <div>
                                <label>n d'immatriculation</label>
                                <input type="text" id="immatriculation" value={this.state.data.subscribe_number} className={styleRetraite.inputForm} />
                            </div>
                            <h3 className={styleRetraite.titleRetraite} >Suivi de dossier</h3>
                            <div className={styleRetraite.labelDemarche}>
                                <label>Dmarche faite le </label>
                                <label>Acte reu le </label>
                                <label>Dmarche faite par</label>
                            </div>
                            <div className={styleRetraite.adresse}>
                                <input type="text" id="faite" placeholder="12/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                <input type="text" id="recu" placeholder="13/12/2017" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                                <input type="text" id="faitePar" placeholder="Mr Dupont Pierre" className={[styleRetraite.inputForm, styleRetraite.adaptSizeT].join(' ')} />
                            </div>
                            <div>
                                <label>suite donne ou  donner</label>
                                <input type="text" placeholder=" imprimer" className={styleRetraite.inputForm} />
                            </div>
                            <div className={styleRetraite.divButton} >
                                <h3 className={styleRetraite.titleRetraite} >Courrier type</h3>
                                <button className={styleRetraite.buttonCustom} onClick={this.handleLetter}  >
                                    METTRE A JOUR
                                </button>
                                <button className={styleRetraite.buttonCustom} onClick={this.printElem}  >
                                    IMPRIMER
                                </button>
                            </div>
                            <h3> {this.state.error} </h3>
                        </div>

                    </div>
                    <page className={styleRetraite.afour} id='letter'  >
                        <div className={styleRetraite.expediteur}  >
                            <p>Expditeur</p>
                            <p><span>{this.state.faitePar}</span></p>
                            <p><span>............................</span></p>
                        </div>
                        <div className={styleRetraite.ToCaisse}  >
                            <p>A la caisse de retraite</p>
                            <p><span> {this.state.caisse} </span></p>
                            <p><span> {this.state.numeroAdresse} {this.state.rue}</span></p>
                            <p><span> {this.state.postale} {this.state.ville} {this.state.pays} </span></p>
                            <p className={styleRetraite.date}  >Le <span>{this.state.faite}</span></p>
                        </div>
                        <div className={styleRetraite.contentLetter}  >
                            <p>Madame, Monsieur,</p>
                            <p>
                                Suite au dcs de Monsieur (Madame ou Mademoiselle), <span>.............</span>, mon/ma (indiquer le lien de parent)
                                <span>...........</span>, survenu le <span>.............</span>, n d'inscription <span>{this.state.immatriculation}</span>,
                                je procde aux formalits qui m'incombent.
                            </p>
                            <p>
                                A ces fins, je vous saurais gr de : <br />
                                - vrifier mes droits ou ceux de <span>...............................</span> au capital dcs,<br />
                                - faire valoir mes droits ou ceux de <span>.........................</span>  la pension de rversion,<br />
                                - de procder au rglement des arrirs dus  la date du dcs.<br />

                            </p>
                            <p>
                                Je vous prie d'agrer, Madame, Monsieur, l'expression, de mes sentiments les meilleurs.
                            </p>
                            <p className={styleRetraite.signature}  >Signature</p>
                        </div>
                        <div className={styleRetraite.mention} >
                            <p>P.J. : acte de dcs</p>
                            <p>Courrier en recommand avec accus de rception</p>
                        </div>
                    </page>
                </div>
            </Page>
            : <div>Chargement ...</div>
        );
    }
}



